<template>
    <div>
        <Row :gutter="20" type="flex" justify="center" align="middle" class="code-row-bg">
            <Col v-for="(item, index) in genealogyList" :key="index" span="3">
            <Card style="max-width:320px">
                <Form>
                    <FormItem>
                        <Avatar size="large" shape="square"
                            :style="{ background: ColorList[Math.floor(Math.random() * ColorList.length)] }">
                            {{ item.firstName }}
                        </Avatar>
                    </FormItem>
                    <FormItem>
                        <h2>{{ item.nickname }}</h2>
                    </FormItem>
                    <FormItem>
                        <h6>{{ item.describe }}</h6>
                    </FormItem>
                    <FormItem>
                        <Button type="info" ghost @click="toCompleteGenealogy(item.id)">详情</Button>
                    </FormItem>
                </Form>
            </Card>
            </Col>
        </Row>
    </div>
</template>

<script>
import { getGenealogyList } from '@/api/Member'
export default {
    data() {
        return {
            genealogyList: [],

            ColorList: ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae'],
        }
    },

    methods: {
        init() {
            getGenealogyList().then(res => {
                this.genealogyList = res.data;
            }).catch(err => {
                this.$Notice.warning({
                    title: '网络异常',
                    desc: '未知原因未能获取到族谱数据!'
                });
            });
        },

        toCompleteGenealogy(gid) {
            this.$router.push({ name: 'Genealogy', params: { gid: gid } });
        },
    },

    mounted() {
        this.init();
    },
}
</script>

<style>
</style>


